@font-face {
    font-family: 'GothicA1-Light';
    src: url('https://cdn.jsdelivr.net/gh/projectnoonnu/noonfonts_2205@1.0/GothicA1-Light.woff2') format('woff2');
    font-weight: 300;
    font-style: normal;
}

@keyframes fadeIn {
    0% {
        opacity: 0;
        transform: translateY(20px);
    }

    50% {
        opacity: 0.3;
    }
    70% {
        opacity: 0.4;
    }

    85% {
        opacity: 0.6;
    }

    100% {
        opacity: 1;
        transform: none;
    }
}

@keyframes NotHover {
    0% {
        opacity: 0;
    }

    50% {
        opacity: 0.5;
    }

    100% {
        opacity: 1;
        transform: none;
    }
}



@keyframes Hover {
    0% {
        opacity: 0;
    }

    50% {
        opacity: 0.5;
    }

    100% {
        opacity: 1;
        transform: none;
    }
}

@media (max-width: 700px) {
    .img {
        width: 120px;
        margin-top: 30px;
    }

    .name {
        font-size: 15px;
    }

    .App {
        font-size: 12px;
        width: 100%;
    }

    .line {
        width: 200px;
        color : #707070;
    }

    .content {
        width: 100%;
        display: block;
    }

    .line {
        margin-top: 60px;
        width: 300px;
        color : #707070;
    }
}

@media (min-width: 700px) {

    .content {
        width: 100%;
        display: flex;
        justify-content: center;
    }

    .line {
        margin-top: 60px;
        width: 600px;
        color : #707070;
    }

}

.App {
    text-align: center; 
    margin-top: 15px;
    font-family: 'GothicA1-Light';
    color : #707070;
    letter-spacing: -1.3px;
    font-size: 13px;
    animation: fadeIn 1200ms ease-out;
}

.name {
    font-size: 20px;
}

.header {
    margin-top: 40px;
    animation: fadeIn 800ms ease-out;
}

.introduce {
    margin-top: 40px;
}

.img_effect {
    animation: fadeIn 200ms ease-out;
}

.img {
    width: 240px;
    margin-top: 30px;
    transition: all 3s;
    animation: Hover 400ms;
}

.img:hover {
    animation: NotHover 400ms ease-out;
}

